import { Injectable } from '@angular/core';

@Injectable()
export class CustomValidateMsgService {

  constructor() { }

  getValidatorErrorMessage(validatorName: string, validatorValue?: any) {
    const config = {
      'required': '*wwwwww',
      'invalidCreditCard': '',
      'invalidEmailAddress': '',
      'invalidPassword': '. 6~20',
      'invalidLength': `${validatorValue.requiredLength}`,
      'invalidMinlength': ` ${validatorValue.minLength}`,
      'invalidMaxlength': ` ${validatorValue.maxLength}`,
      'invalidMinValue': `${validatorValue.minValue}`,
      'invalidMaxValue': `${validatorValue.maxValue}`,
      'invalidChs' : '',
      'invalidEnglish' : '',
      'invalidZip' : '',
      'invalidQq' : 'QQ',
      'invalidAge' : '0120',
      'invalidPhone' : ',:020-88888888',
      'invalidIntOrFloat' : '',
      'invalidInt' : '',
      'invalidIntandzero' : '0',
      'invalidMobile' : '',
      'invalidPhoneOrMobile' : ',:020-88888888',
      'invalidLoginName' : '',
      'invalidUsername' : '6-16',
      'invalidName' : '',
      'invalidUnnormal' : '',

      'minlength': ` ${validatorValue.requiredLength}`
    };

    return config[validatorName];
  }
}



